<template>
  <div class="DashboardSurveysNew">
    <el-form
      ref="surveyForm"
      :model="survey"
      class="survey-form">

      <div class="body">
        <h2 class="title">Создать новый опрос
          <small class="small">
            Вы сможете изменить эти поля и произвести дополнительные настройки позднее
          </small>
        </h2>
        <el-form-item
          :required="true"
          label="Обязательное поле">
          <el-input placeholder="Название опроса" v-model="survey.name" autofocus />
        </el-form-item>
        <el-form-item label="Дополнительно">
          <el-input placeholder="Описание" v-model="survey.description" />
        </el-form-item>
        <el-form-item>
          <el-input placeholder="Приветственное сообщение" v-model="survey.start_message" />
        </el-form-item>
        <el-form-item>
          <el-input placeholder="Сообщение по окончании" v-model="survey.finish_message" />
        </el-form-item>
        <!--el-form-item label="Базовая подсказка">
          <el-input placeholder="" v-model="survey.hint" />
        </el-form-item-->
        <el-form-item label="Баллов за прохождение">
          <el-input-number
            :min="0"
            v-model="survey.price" />
        </el-form-item>
        <el-form-item label="Доступ только по ссылке">
          <el-switch v-model="survey.link_only" />
        </el-form-item>
      </div>
      <el-form-item class="bottom">
        <el-button @click="$router.go(-1)">Отменить</el-button>
        <el-button
          type="primary"
          @click="submitForm">Создать опрос и перейти к заполнению</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'DashboardSurveysNew',

  data() {
    return {
      survey: {
        name: '',
        start_message: '',
        description: '',
        finish_message: '',
        link_only: false,
        hint: '',
        price: 20,
      },
    };
  },

  methods: {
    async submitForm() {
      this.$http.post('/surveys', { survey: this.survey })
        .then((response) => {
          this.$message.success('Вы успешно создали опрос. Осталось заполнить.');
          this.$router.push({ name: 'DashboardSurveysShow', params: { id: response.data.id } });
        });
    },
  },
};
</script>

